
.side-drawer {
height: 100%;
background: wheat;
box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
position: fixed;
top: 0;
left:0 ;
width:  70%;
max-width: 400px;
z-index: 200;
transform: translate(-100%);
transition:transform 0.3s ease-in-out;
}

.side-drawer.open {
    transform:translate(0) ;
}